// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import $ from 'jquery'

Vue.prototype.$ = $

Vue.config.productionTip = false

Vue.filter('formatDate',function(str){
  if(!true) return ''
  var date = new Date(str);    //处理成这种格式：Mon Oct 08 2018 15:31:59 GMT+0800 (中国标准时间)
  var time = parseInt( new Date().getTime() - date.getTime())/1000  //现在的时间减去传入的时间
  if(time < 30){
    return '刚刚'
  }else if( time < 60){
    return parseInt(time) + '秒前'
  }else if( time/60 < 60){    //3600秒一个小时
    return parseInt(time/60) + '分钟前'
  }else if( time/60/60 < 24){
    return parseInt(time/60/60) + '小时前'
  }else if(time/60/60/24 < 30){
    return parseInt(time/60/60/24) + '天前'
  }else if(time/60/60/24/30 < 12){
    return parseInt(time/60/60/24/30)+'月前'
  }else{
    return parseInt(time/60/60/24/30/12) +'年前'
  }
})
Vue.filter('ArticleType',function(data){
  if(data.top === true){
    return '置顶'
  }else if(data.good === true){
    return '精华'
  }else if(data.tab === 'share'){
    return '分享'
  }else if(data.tab === 'ask'){
    return '问答'
  }else{
    return '招聘'
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})




